body {
    margin: 0;
    font-family: 'Bebas Neue', sans-serif;
    font-size: 18px;
    color: #fff;
    -webkit-font-smoothing: antialiased;
    background-color: #fff;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
  }
  p {
    margin: 0 0 10;
  }
  *,
  *:before,
  *:after {
    box-sizing: border-box;
  }
  
.container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
  }
  a{
      text-decoration: none;
      color: #fff;
  }
  .subtitle{
    font-family: Bebas Neue;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 24px;
    letter-spacing: 2px;
    color: #FFFFFF; 
  }
  .text{
    font-family: Open Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    color: #FFFFFF;
  }
  .bigtitle{
    font-weight: 400;
    font-size: 80px;
    line-height: 80px;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: #FFFFFF;
  }
  .btn--yellow{
    background: linear-gradient(182.88deg, #FFB548 11.27%, #F3AF34 90.4%);
    border-radius: 4px;
    padding:20px 22px ;
    border: none;
    cursor:pointer;
    outline:none;

    font-family: Open Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 22px;
    /* identical to box height */

    text-align: center;

    color: #1A1917;

    &:hover{
        background: rgb(255, 183, 49);
    }
    
  }

.header{
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #c9913d;
        z-index: 3;
    } /* #6f552a */
    &__select{
        position: relative;
        z-index: 5;
        margin-left: 170px;
        .is-active .header__select-body{
            display: block;
        }
        &-header{
            display: flex;
            position: relative;
            cursor:pointer;
            color: #fff;
            padding-right: 35px;
            border-right: 1px solid #fff;
            &:hover{
                color: #FFB548;
            }
            &:hover .header__select-icon-fill{
                fill: #FFB548;
            }
            .header__select-icon-fill{
                fill: #fff;
            }
        }
        &-body{}
        &-icon{
            position: absolute;
            left: 25px;
            bottom: 4px;
            img{
            }
        }
        &-current{
        }
        &-item{
            color: #fff;
        }
        &-body{
            display: none;
            background-color: black;
            border-radius: 4px;
            padding:5px 20px 5px 5px;
            color: #fff;
            transition: color .2s linear;
            cursor: pointer;
            &:hover{
                color: #FFB548;
            }
            
        }
        &-item a{
            transition: color .2s linear;
            &:hover{
                color: #FFB548;
            }
        }
    }
    &__inner{
        padding: 20px;
        display: flex;
        align-items: center;
        position: relative;//вывел контентную часть на второй план
    }
    &__logo{
        width: 55px;
        height: 55px;
        margin-left: 20px;
        overflow: hidden;
        z-index: 3;
        position: relative;
    }
    .header-nav__menu{
        display: block;
        z-index: 5;
        margin-left: 200px;
        .nav__link{
            color: #fff;
            text-transform: uppercase;
            transition: color .2s linear;
            +a{
                margin-left: 38px;
            }
            &:hover{
                color: #FFB548;
            }
        }
    }
    &__icons{
        position: relative;
        z-index: 3;
        height:30px;
        &-inner{
            padding-left: 24px;
        }
        &-xbox{
            position: absolute;
            cursor: pointer;
            width: 30px;
            height: 27px;
            left: 24px;
            &--fill{
                fill: #FFB548;
            }
            &--fill:hover{
                fill: rgb(255, 168, 36);
            }
        }
        &-steam{
            margin-left: 13px;
            position: absolute;
            left: 60px;
            width: 30px;
            height: 27px;
            cursor: pointer;
            &--fill{
                fill: #FFB548;
            }
            &--fill:hover{
                fill: rgb(255, 168, 36);
            }
        }
    }
    .burger{
        display: none;
    }
    @media (max-width:1200px) {
        .header-nav__menu{
            position: fixed;
            top: -100%;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: #c9913d;
            margin-left: 0;
            z-index: 2;
            display: flex;
            padding: 90px 0 20px 30px;  
            transition: all .2s ease-in-out;
            overflow: auto;
        }
        .header-nav__menu.active{
            top: 0;
            flex-direction: column;
        }
        .wrapper__burder-select{
            display: flex;
        }
        .header__inner{
            padding: 10px 0 10px 0;
            justify-content: space-between;
            
        }
        .header__select{
            z-index: 3;
            margin-right: 25px;
        }
        .header__icons{
            position: absolute;
            z-index: 2;
        }
        .burger{
            cursor: pointer;
            float: right;
            display: block;
            position: relative;
            width: 30px;
            height: 20px;
            z-index: 3;
            span{
                position: absolute;
                background-color: #fff;
                left: 0;
                width: 100%;
                height: 2px;
                top: 9px;
                transition: all .3s ease-in-out;
            }
            &:before,
            &:after{
                content: '';
                background-color: #fff;
                position: absolute;
                width: 100%;
                height: 2px;
                left: 0;
                transition: all .2s ease-in-out;
            }
            &:before{
                top: 0;
            }
            &:after{
                bottom: 0;
            }
        }
        .burger.active:before{
            transform: rotate(45deg);
            top:9px;
        }
        .burger.active:after{
            transform: rotate(-45deg);
            bottom: 9px;
        }
        .burger.active span{
            transform: scale(0);
        }
    }
}
@media(max-width:1200px){
    .header-nav__menu .nav__link + a {
        margin-left: 0;
    }
    body.lock{
        overflow: hidden;
    }
}


